//contact//
.contact-container{
    @include viewport(small) {
        max-width: 400px;
     }
     @include viewport(medium) {
        max-width: 700px;
     }
}
.first-name {
        @include viewport(x-large) {
        grid-column-start: 1;
        grid-column-end: 2;
        }
    }
.last-name {
    @include viewport(x-large) {
    grid-column-start: 2;
    grid-column-end: 3;
}
}
.email {
    @include viewport(x-large) {
        grid-column-start: 1;
        grid-column-end: 2;
        }
}  
.phone {
    @include viewport(x-large) {
        grid-column-start: 2;
        grid-column-end: 3;
}
}
@media (min-width: 1260px){
    .subject{
        grid-column-start: 1;
        grid-column-end: 3;
    }
    }
@media (min-width: 1260px){
    .message {
        grid-column-start: 1;
        grid-column-end: 3;
    }
    }
    
