//projects//

.projects-block-tools{
    @include viewport(small) {
        display: flex;
        flex-wrap: wrap;
        justify-content: center; 
     }
    @include viewport(medium) { 
        display: flex;
        flex-direction: row;
        justify-content: center; 
    }
    @include viewport(large) {
        display: flex;
        flex-wrap: wrap;
        justify-content: center; 
     }
     @include viewport(x-large) {
        display: flex;
        flex-wrap: wrap;
        justify-content: center; 
     }    
}

.projects-block-tools li{
@include viewport(small) {
   font-size: 1.8rem;
}
@include viewport(medium) { 
    font-size: 1.8rem; 
}
}
.symbol{
    @include viewport(small) {
       font-size: 3rem;
    }
    @include viewport(medium) { 
        font-size: 3rem; 
    }
    }
.projects-block-button{
    @include viewport(small) {
        margin-bottom: 20px;
     }
     @include viewport(medium) {
        margin-bottom: 20px;
     }
}

.projects-block{
    @include viewport(small) {
        margin: 20px 10px;
    }
    @include viewport(medium) {   
    max-width: 100%;
    margin: 20px 10px;
    }
    @include viewport(large) {   
        margin: 30px 10px
        }
    
    @include viewport(x-large) {   
        margin: 30px 10px
        }
        @include viewport(xx-large) {   
            margin: 45px 10px
            }
}
.project-grid {
    @include viewport(small) {
        display: flex;
        flex-direction: column;
    }
    @include viewport(medium) {
        max-width: 600px;
    margin: 0 auto;
    }
    @include viewport(large) {
        grid-template-columns: repeat(2, 1fr);
        max-width: none;
        margin: 0 1rem;
    }
    @include viewport(x-large) {
        grid-template-columns: repeat(3, 1fr);
        margin: 0 1rem;
    }
    @include viewport(xx-large) {
        grid-template-columns: repeat(3, 1fr);    
    margin: 0 2rem;
    gap: 2rem
    }
 }
 .projects-grid{
    @include viewport(small) {
        margin: 0 20px;
    }   
 }
 .projects-block:hover {
    @include viewport(small) {
        transform: translate(0, -5px);
    transform: scale(0.95);
    }
    @include viewport(medium) {
        transform: translate(0, -5px);
    transform: scale(0.95);
    }
    
  }
  .projects{
    @include viewport(x-large) {
        width: 100%;
    }
    @include viewport(xx-large) {
        width: 100%;
    } 
  }
  .projects-block-tools h5{
    @include viewport(small) {
        display: none;
    }
    @include viewport(medium) {
        display: none;
    }  
  }
